import _jsonrpc from '@webui-one/core/jsonrpc';
import { isNonEmptyString } from '@webui-one/core/utils/string';
import { maybeGetModels } from 'modules/models/actions';
import { read } from 'modules/transactions/actions';
import * as actionTypes from './actionTypes';
import { selectHasSchemaFromCurrentPath as _selectHasSchemaFromCurrentPath } from './selectors';

export const getChildrenNamesStartAction = () => ({
    type: actionTypes.GET_CHILDREN_NAMES_START,
});

export const getChildrenNamesErrorAction = error => ({
    type: actionTypes.GET_CHILDREN_NAMES_ERROR,
    error,
});

export const getChildrenNamesCompleteAction = (
    path,
    result,
    isList,
    schema,
    showActions = true,
    showOperdata = true,
) => ({
    type: actionTypes.GET_CHILDREN_NAMES_COMPLETE,
    path,
    result,
    schema,
    isList,
    showActions,
    showOperdata,
});

const queryListChildrenNames = async (path, th, schema, matchString, chunkSize, jrpc) => {
    const keys = schema.data.key;
    let xpathExpr;
    if (matchString !== '') {
        xpathExpr = `${schema.data.name}[starts-with(${keys[0]}, "${matchString}")]`;
    }
    const params = {
        th,
        path,
        selection: keys,
        result_as: 'string',
        xpath_expr: xpathExpr,
        context_node: path.substr(0, path.lastIndexOf('/')),
        chunk_size: chunkSize,
    };
    return jrpc('query', params);
};

export const getChildrenNamesThunk = ({
    path,
    matchString,
    showActions,
    showOperdata,
    chunkSize = 10,
    funcs: {
        rd = read,
        getModels = maybeGetModels,
        jsonrpc = _jsonrpc,
        selectHasSchemaFromCurrentPath = _selectHasSchemaFromCurrentPath,
    } = {},
} = {}) => async (dispatch, getState) => {
    if (!isNonEmptyString(path) || path === '/') {
        return dispatch(getModels());
    }
    dispatch(getChildrenNamesStartAction());
    try {
        const { th } = await dispatch(rd());
        const state = getState();
        const hasSchemaFromCurrentPath = selectHasSchemaFromCurrentPath(state, path);
        let schema;

        if (hasSchemaFromCurrentPath) {
            schema = state.breadcrumbs.schema;
        } else {
            schema = await jsonrpc('get_schema', {
                path,
                th,
                levels: 1,
                evaluate_when_entries: true,
            });
        }

        if (schema.data.kind === 'list') {
            const result = await queryListChildrenNames(
                path,
                th,
                schema,
                matchString,
                chunkSize,
                jsonrpc,
            );
            return dispatch(
                getChildrenNamesCompleteAction(
                    path,
                    result,
                    true,
                    schema,
                ),
            );
        }
        return dispatch(
            getChildrenNamesCompleteAction(
                path,
                schema,
                false,
                null,
                showActions,
                showOperdata,
            ),
        );
    } catch (error) {
        return dispatch(getChildrenNamesErrorAction(error));
    }
};

export const resetAction = () => ({
    type: actionTypes.RESET,
});
