import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { withRouter } from '@webui-one/router';
import { applicationNameSetAction } from 'modules/application/actions';
import { formatPath } from '@webui-one/core/utils/path';
import { isNonEmptyString, isString } from '@webui-one/core/utils/string';
import DataContainer from 'Common/Components/ConfigRender/DataContainer';
import CheckBox from '@webui-one/components/ConfigRender/CheckBox/CheckBox';
import { checkboxStyle } from '@webui-one/styles/ConfigurationEditorStyles';
import {
    subscribeCdbOperAction,
    subscribeChangesAction,
    unsubscribeCdbOperAction,
    unsubscribeChangesAction,
} from 'modules/comet/actions';
import { updateConfig } from 'modules/comet/configeditor';
import { selectPreviousUrl } from 'modules/routing/reducer';
import { selectIsOperdata } from 'modules/schemas/reducer';
import { isBigYangPath } from 'modules/schemas/utils';
import {
    deleteAction,
    deleteNodeAndChildrenAction,
    getValueAction,
    modifiedNodesAction,
    createAction,
} from 'modules/nodes/actions';
import { tryGetTransChanges } from 'modules/transactions/actions';
import { selectActionPath } from 'modules/transactions/selectors';
import {
    registerShortcut,
    unregisterShortcut,
} from 'modules/shortcuts/actions';
import ipcPropTypes from 'modules/ipc/propTypes';
import ConfigurationEditor from './ConfigurationEditor';
import { CONFIGURATION_EDITOR_NAME } from './defines';
import {
    showActionsAction,
    showEmptyAction,
    showOperdataAction,
} from './actions';
import {
    selectApplicationConfiguration,
    selectIsEditMode,
    selectShowEmpty,
    selectShowOperdata,
    selectShowActions,
    selectIncludeSubfolders,
    selectIpcModification,
} from './reducer';
import { pathToObjects } from '@webui-one/core/keypath/kp-parser';
import PackagesAndModulesContainer from './PackagesAndModulesContainer';

const routeToPath = route => (
    isString(route) && route.indexOf('/') !== 0
        ? `/${route}`
        : route
);

const getSubUri = (subPath) => {
    if (!subPath || subPath === '/') return '';
    return pathToObjects(subPath).pop().uri;
};

class ConfigurationEditorContainer extends React.Component {
    constructor(props) {
        super(props);
        this.route = '';
        this.shortcuts = ['vo', 'va', 'vc', 'vm'];
        this.shortcutHandler = this.shortcutHandler.bind(this);
        this.navigateModel = this.navigateModel.bind(this);
        this.onCometUpdate = this.onCometUpdate.bind(this);
    }

    componentDidMount() {
        const {
            actionPath,
            isOperdata,
            onGetTransChanges,
            onSetApplicationName,
            onRegisterShortcut,
        } = this.props;
        onSetApplicationName(CONFIGURATION_EDITOR_NAME);
        this.updateRoute(isOperdata);

        onRegisterShortcut(this.shortcuts, this.shortcutHandler);

        if (isNonEmptyString(actionPath)) {
            onGetTransChanges(actionPath);
        }
    }

    componentDidUpdate(prevProps) {
        const { actionPath, onGetTransChanges } = this.props;
        const { actionPath: prevActionPath, isOperdata } = prevProps;
        this.updateRoute(isOperdata);

        if (actionPath !== prevActionPath) {
            onGetTransChanges(actionPath);
        }
    }

    componentWillUnmount() {
        const {
            isOperdata,
            onUnregistShortcut,
            onUnsubscribe,
        } = this.props;
        onUnregistShortcut(this.shortcuts, this.shortcutHandler);
        onUnsubscribe(
            routeToPath(this.route),
            this.onCometUpdate,
            isOperdata,
        );
    }

    onCometUpdate(notifications, subscription) {
        const {
            onCreate,
            onDelete,
            onModifiedNodes,
            onUpdateLeaf,
        } = this.props;
        updateConfig(
            notifications,
            subscription.params.path,
            {
                onCreate,
                onDelete,
                onModifiedNodes,
                onUpdateLeaf,
            },
        );
    }

    updateRoute(wasOperdata) {
        const {
            isOperdata,
            route: newRoute,
            onSubscribe,
            onUnsubscribe,
        } = this.props;
        const oldRoute = this.route;
        if (typeof newRoute === 'string' && newRoute !== this.route) {
            this.route = newRoute;
        }
        if (oldRoute !== this.route) {
            onSubscribe(
                routeToPath(this.route),
                this.onCometUpdate,
                isOperdata,
            );
            onUnsubscribe(
                routeToPath(oldRoute),
                this.onCometUpdate,
                wasOperdata,
            );
        }
        return this.route;
    }

    navigateModel(subPath) {
        const { router } = this.props;
        const baseUrl = CONFIGURATION_EDITOR_NAME;
        const url = formatPath(`${baseUrl}${getSubUri(subPath)}`);
        router.push(url);
    }

    shortcutHandler(shortcut) {
        const {
            onSetHideActions,
            onSetShowEmpty,
            onSetHideOperdata,
            applicationConfiguration,
        } = this.props;
        const {
            showActions,
            showEmpty,
            showOperdata,
        } = applicationConfiguration;

        if (shortcut === 'vo') {
            onSetHideOperdata(showOperdata);
        } else if (shortcut === 'va') {
            onSetHideActions(showActions);
        } else if (shortcut === 'vm') {
            onSetShowEmpty(!showEmpty);
        }
    }

    createCheckboxes() {
        const {
            onSetHideActions,
            onSetShowEmpty,
            onSetHideOperdata,
            applicationConfiguration,
        } = this.props;
        const {
            showActions,
            showEmpty,
            showOperdata,
        } = applicationConfiguration;
        return (
            <div style={checkboxStyle.checkboxWrapper} key="incSub">
                <div style={checkboxStyle.group}>
                    <CheckBox
                        editable
                        value={!showEmpty}
                        onSetValue={value => onSetShowEmpty(!value)}
                        name="config-only"
                        styleName="radio"
                    >
                        <div style={checkboxStyle.label}>
                            configuration only
                        </div>
                    </CheckBox>
                </div>
                <CheckBox
                    editable
                    value={showEmpty}
                    onSetValue={onSetShowEmpty}
                    name="full-model"
                    styleName="radio"
                >
                    <div style={checkboxStyle.label}>
                        full model
                    </div>
                </CheckBox>
                <CheckBox
                    editable={showEmpty}
                    value={!showActions}
                    onSetValue={onSetHideActions}
                    name="hide-actions"
                    styleName="padded"
                >
                    <div style={showEmpty ? checkboxStyle.label : checkboxStyle.labelDisabled}>
                        hide actions
                    </div>
                </CheckBox>
                <CheckBox
                    editable={showEmpty}
                    value={!showOperdata}
                    onSetValue={onSetHideOperdata}
                    name="hide-oper"
                    styleName="padded"
                >
                    <div style={showEmpty ? checkboxStyle.label : checkboxStyle.labelDisabled}>
                        hide operational data
                    </div>
                </CheckBox>
            </div>
        );
    }

    render() {
        const {
            includeSubfolders,
            ipcModification,
            isEditMode,
            routeParams,
            showActions,
            showEmpty,
            showOperdata,
        } = this.props;
        const showPackagesAndModules = (routeParams.splat === '');
        const isBigYang = isBigYangPath(routeParams.splat);
        const enableSubfolders = isBigYang ? false : includeSubfolders;

        return (
            <ConfigurationEditor
                onNavigate={this.navigateModel}
                routeParams={routeParams}
                checkboxes={this.createCheckboxes()}
                subfoldersAreDisabled={isBigYang}
            >
                {showPackagesAndModules ? <PackagesAndModulesContainer /> : (
                    <DataContainer
                        applicationName={CONFIGURATION_EDITOR_NAME}
                        includeSubfolders={enableSubfolders}
                        ipcModification={ipcModification}
                        isEditMode={isEditMode}
                        path={routeParams.splat}
                        showActions={showActions}
                        showEmpty={showEmpty}
                        showOperdata={showOperdata}
                        showBackToSourceLink
                    />
                )}
            </ConfigurationEditor>
        );
    }
}

ConfigurationEditorContainer.propTypes = {
    actionPath: PropTypes.string,
    applicationConfiguration: PropTypes.shape(Object),
    includeSubfolders: PropTypes.bool,
    ipcModification: ipcPropTypes.ipcModification,
    isEditMode: PropTypes.bool,
    isOperdata: PropTypes.bool,
    route: PropTypes.string,
    onGetTransChanges: PropTypes.func,
    onUpdateLeaf: PropTypes.func,
    onCreate: PropTypes.func,
    onDelete: PropTypes.func,
    onModifiedNodes: PropTypes.func,
    onRegisterShortcut: PropTypes.func,
    onSetApplicationName: PropTypes.func.isRequired,
    onSetHideActions: PropTypes.func.isRequired,
    onSetHideOperdata: PropTypes.func.isRequired,
    onSetShowEmpty: PropTypes.func.isRequired,
    onSubscribe: PropTypes.func.isRequired,
    onUnregistShortcut: PropTypes.func,
    onUnsubscribe: PropTypes.func.isRequired,
    routeParams: PropTypes.shape(Object).isRequired,
    router: PropTypes.shape(Object),
    showActions: PropTypes.bool,
    showEmpty: PropTypes.bool,
    showOperdata: PropTypes.bool,
};


const mapStateToProps = (state, ownProps) => {
    const routeParams = (ownProps.routeParams) || {};
    const { splat: route } = routeParams;
    return ({
        actionPath: selectActionPath(state, route),
        applicationConfiguration: selectApplicationConfiguration(state),
        includeSubfolders: selectIncludeSubfolders(state),
        ipcModification: selectIpcModification(state),
        isEditMode: selectIsEditMode(state),
        isOperdata: selectIsOperdata(state, route),
        route,
        previousUrl: selectPreviousUrl(state),
        showActions: selectShowActions(state),
        showEmpty: selectShowEmpty(state),
        showOperdata: selectShowOperdata(state),
    });
};

const mapDispatchToProps = (
    dispatch,
    {
        funcs: {
            getTransChanges = tryGetTransChanges,
        } = {},
    } = {},
) => ({
    onGetTransChanges(path) {
        dispatch(getTransChanges({ path }));
    },
    onModifiedNodes(paths) {
        dispatch(modifiedNodesAction(paths));
    },
    onUpdateLeaf(path, value) {
        dispatch(getValueAction(path, value));
    },
    onDelete(path, deleteChildren = false) {
        if (deleteChildren !== true) {
            dispatch(deleteAction(path));
        } else {
            dispatch(deleteNodeAndChildrenAction(path));
        }
    },
    onCreate(path) {
        dispatch(createAction(path));
    },
    onRegisterShortcut(shortcuts, handler) {
        dispatch(registerShortcut(shortcuts, handler));
    },
    onUnregistShortcut(shortcuts, handler) {
        dispatch(unregisterShortcut(shortcuts, handler));
    },
    onSetApplicationName(name) {
        dispatch(applicationNameSetAction(name));
    },
    onSetHideActions(value) {
        dispatch(showActionsAction(!value));
    },
    onSetShowEmpty(value) {
        dispatch(showEmptyAction(value));
    },
    onSetHideOperdata(value) {
        dispatch(showOperdataAction(!value));
    },
    onSubscribe(path, handler, isOperdata) {
        if (isOperdata) {
            return dispatch(subscribeCdbOperAction([path], handler));
        }
        return dispatch(subscribeChangesAction([path], handler));
    },
    onUnsubscribe(path, handler, isOperdata) {
        if (isOperdata) {
            return dispatch(unsubscribeCdbOperAction([path], handler));
        }
        return dispatch(unsubscribeChangesAction([path], handler));
    },
});

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(withRouter(ConfigurationEditorContainer));

export {
    mapStateToProps as _mapStateToProps,
    mapDispatchToProps as _mapDispatchToProps,
    routeToPath as _routeToPath,
    ConfigurationEditorContainer as _ConfigurationEditorContainer,
};
