import React from 'react';
import PropTypes from 'prop-types';
import {
    truncateLongString,
} from '@webui-one/core/utils/string';
import Loader from '@webui-one/components/Loading/Loader';
import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';
import { input as style } from './BreadcrumbStyles';

const DROP_DOWN_LIMIT = 20;

const sortNames = (name1, name2) => {
    if (name1.name.toLowerCase() > name2.name.toLowerCase()) return 1;
    return -1;
};

const updateListNames = (props, state) => {
    const { childrenNames } = props;
    const input = state.inputValue.toLowerCase();
    const nameMatch = [];
    const namespaceMatch = [];
    const contains = [];
    (childrenNames || []).forEach((childName) => {
        const fullName = childName;
        const colonIndex = fullName.indexOf(':');
        if (input === '') {
            nameMatch.push({ name: fullName, fullName });
            return;
        }
        let name = fullName;
        if (colonIndex > -1) {
            name = fullName.substr(colonIndex + 1);
        }
        if (name.toLowerCase().startsWith(input)) {
            let duplicate = false;
            nameMatch.forEach((item) => {
                if (item.name === name) {
                    // eslint-disable-next-line no-param-reassign
                    item.name = item.fullName;
                    duplicate = true;
                }
            });
            nameMatch.push({ name: duplicate ? fullName : name, fullName });
            return;
        }
        if (fullName.toLowerCase().startsWith(input)) {
            namespaceMatch.push({ name: fullName, fullName });
            return;
        }
        if (fullName.toLowerCase().includes(input)) {
            contains.push({ name: fullName, fullName });
        }
    });
    nameMatch.sort(sortNames);
    namespaceMatch.sort(sortNames);
    return { nameMatch, namespaceMatch, contains };
};

class BreadcrumbInput extends React.Component {
    constructor(props) {
        super(props);
        this.handleBlur = this.handleBlur.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.handleFocus = this.handleFocus.bind(this);
        this.handleKeyDown = this.handleKeyDown.bind(this);
        this.state = {
            autoCompleteIsVisible: false,
            dropDownIndex: undefined,
            nameMatch: [],
            namespaceMatch: [],
            contains: [],
            inputValue: '',
        };
    }

    static getDerivedStateFromProps(props, state) {
        const { childrenNames, isList } = props;
        if (!state.autoCompleteIsVisible) return null;
        if (isList) {
            return {
                nameMatch: childrenNames.map(name => ({ name, fullName: name })),
                namespaceMatch: [],
                contains: [],
            };
        }
        return updateListNames(props, state);
    }

    handleEnter(dropDownIndex, listNames, names, inputValue) {
        if (dropDownIndex !== undefined && listNames.length > dropDownIndex) {
            this.navigate(
                listNames[dropDownIndex].fullName,
            );
        } else if (inputValue === '') {
            this.navigate('');
        } else if (names.length > 0) {
            const fullMatch = names.filter(name => name.name === inputValue)[0];
            if (fullMatch) {
                this.navigate(fullMatch.fullName);
            } else if (names.length === 1) {
                this.navigate(names[0].fullName);
            }
        }
    }

    handleArrowUpDown(e, dropDownIndex, listNames) {
        e.preventDefault();
        const nrOfNames = listNames.length;
        if (nrOfNames > 0) {
            let newIndex;
            if (dropDownIndex === undefined) {
                newIndex = e.key === 'ArrowUp' ? nrOfNames - 1 : 0;
            } else {
                const diff = e.key === 'ArrowUp' ? -1 : 1;
                const moduloIndex = Math.min(nrOfNames, DROP_DOWN_LIMIT);
                newIndex = (dropDownIndex + diff + moduloIndex) % moduloIndex;
            }
            this.setState({ dropDownIndex: newIndex });
        }
    }

    handleTab(e, dropDownIndex, listNames, names, inputValue) {
        e.preventDefault();
        if (dropDownIndex !== undefined && listNames.length > dropDownIndex) {
            this.navigateBreadcrumbs(
                listNames[dropDownIndex].fullName,
            );
        } else if (names.length > 1) {
            const fullMatch = names.filter(name => name.name === inputValue)[0];
            if (fullMatch) {
                this.navigateBreadcrumbs(fullMatch.fullName);
            } else {
                let matchingChars = inputValue.length;
                let matchingString = '';
                let matchingStrings = [];
                const doMatch = matchString => (name => name.name.startsWith(matchString));
                let isMatching = true;
                while (isMatching) {
                    matchingChars += 1;
                    matchingString = names[0].name.substr(0, matchingChars);
                    matchingStrings = names.filter(doMatch(matchingString));
                    isMatching = matchingChars <= names[0].name.length
                            && matchingStrings.length === names.length;
                }
                const commonString = names[0].name.substr(0, matchingChars - 1);
                this.setState({ inputValue: commonString });
            }
        } else if (names.length === 1) {
            this.navigateBreadcrumbs(names[0].fullName);
        }
    }

    handleKeyDown(e) {
        const {
            autoCompleteIsVisible,
            contains,
            dropDownIndex,
            nameMatch,
            namespaceMatch,
            inputValue,
        } = this.state;
        const { onReset } = this.props;
        const names = nameMatch.concat(namespaceMatch);
        const listNames = names.concat(contains);
        if (e.key === 'Enter' && autoCompleteIsVisible) {
            this.handleEnter(dropDownIndex, listNames, names, inputValue);
        } else if (e.key === 'Escape') {
            this.setState({ autoCompleteIsVisible: false, inputValue: '' });
            onReset();
            this.input.blur();
        } else if ((e.key === 'ArrowUp' || e.key === 'ArrowDown') && autoCompleteIsVisible) {
            this.handleArrowUpDown(e, dropDownIndex, listNames);
        } else if (e.key === 'Tab' && autoCompleteIsVisible) {
            this.handleTab(e, dropDownIndex, listNames, names, inputValue);
        } else if (e.key === 'Backspace' && inputValue === '') {
            this.navigateBreadcrumbs('', true);
        }
    }

    handleChange() {
        const { isList } = this.props;
        if (isList) {
            const {
                onGetChildrenNames,
            } = this.props;
            onGetChildrenNames(
                this.input.value, DROP_DOWN_LIMIT,
            );
        }
        this.setState({
            dropDownIndex: undefined,
            inputValue: this.input ? this.input.value : '',
        });
    }


    handleBlur() {
        const { onReset } = this.props;
        this.setState({ autoCompleteIsVisible: false, inputValue: '' });
        onReset();
    }

    handleFocus() {
        const { onGetChildrenNames } = this.props;
        onGetChildrenNames();
        this.setState({
            autoCompleteIsVisible: true,
        });
    }

    getAnchorElement(child, matchString, active) {
        const childName = truncateLongString(child.name, 90, 90);
        const pos = childName.search(RegExp(matchString, 'ig'));
        const textBits = [childName.substr(0, pos),
            childName.substr(pos, matchString.length),
            childName.substr(pos + matchString.length)];

        return (
            <a
                role="button"
                tabIndex={0}
                onMouseDown={(e) => { if (e.button === 0) this.navigate(child.fullName); }}
                style={active ? style.deviceGroupsOptionsActive : style.deviceGroupsOptions}
                ref={active && ((r) => { this.deviceGroupsOption = r; })}
                key={child.fullName}
                data-handle={`anchor ${child.fullName} ${active ? 'active' : ''}`}
            >
                {textBits[0] === '' || <span key="a">{textBits[0]}</span>}
                {textBits[1] === '' || <span key="b" style={style.highlight}>{textBits[1]}</span>}
                {textBits[2] === '' || <span key="c" >{textBits[2]}</span>}
            </a>
        );
    }

    navigate(childName) {
        const { onNavigate, isList } = this.props;
        this.setState({ dropDownIndex: undefined, inputValue: '' });
        if (childName === '') onNavigate('');
        else onNavigate(isList ? `{${childName}}` : `/${childName}`);
    }

    navigateBreadcrumbs(childName, isBackNav = false) {
        const { onNavigateBreadcrumbs, isList } = this.props;
        this.setState({ dropDownIndex: undefined, inputValue: '' });
        onNavigateBreadcrumbs(isList ? `{${childName}}` : `/${childName}`, isBackNav);
    }

    render() {
        const {
            autoCompleteIsVisible,
            dropDownIndex,
            nameMatch,
            namespaceMatch,
            contains,
            inputValue,
        } = this.state;
        const { isLoading } = this.props;
        if (this.input) this.input.value = inputValue;
        let filteredContainerNames = [];
        if (autoCompleteIsVisible) {
            const listNames = nameMatch.concat(namespaceMatch.concat(contains));
            filteredContainerNames = listNames.map((child, ix) => {
                if (ix > DROP_DOWN_LIMIT) return null;
                if (ix === DROP_DOWN_LIMIT) {
                    return <div key={`info${ix}`} style={style.deviceGroupsOptions}>...</div>;
                }
                return this.getAnchorElement(
                    child,
                    inputValue,
                    ix === dropDownIndex,
                );
            });
        }

        return (
            <div style={style.labelFilter} data-handle="breadcrumbInput">
                <input
                    style={style.input}
                    ref={(r) => { this.input = r; }}
                    onKeyDown={this.handleKeyDown}
                    data-handle="labelInput"
                    onFocus={this.handleFocus}
                    onBlur={this.handleBlur}
                    onChange={this.handleChange}
                />
                {(autoCompleteIsVisible && (
                    <div
                        ref={(dg) => { this.deviceGroups = dg; }}
                        style={style.labelDropdown}
                    >
                        {(isLoading) && (
                            <Loader color={defaultStyles.color9} />
                        )}
                        {(filteredContainerNames.length > 0 && !isLoading) && (
                            <>
                                {filteredContainerNames}
                            </>
                        )}
                        {(filteredContainerNames.length === 0 && !isLoading) && (
                            <div key={'info'} style={style.deviceGroupsOptions}>No match</div>
                        )}
                    </div>
                ))}
            </div>
        );
    }
}

BreadcrumbInput.propTypes = {
    childrenNames: PropTypes.arrayOf(PropTypes.string),
    isList: PropTypes.bool,
    isLoading: PropTypes.bool,
    onGetChildrenNames: PropTypes.func.isRequired,
    onNavigate: PropTypes.func,
    onNavigateBreadcrumbs: PropTypes.func,
    onReset: PropTypes.func,
};

export default BreadcrumbInput;

export {
    updateListNames as _updateListNames,
};
