import { connect } from 'react-redux';
import { Link } from '@webui-one/router';
import Breadcrumb from '@webui-one/components/Breadcrumb/Breadcrumb';
import {
    getChildrenNamesThunk,
    resetAction,
} from 'modules/breadcrumbs/actions';
import {
    selectChildrenNames,
    selectIsList,
    selectIsLoading,
} from 'modules/breadcrumbs/selectors';
import { selectRootModels } from 'modules/models/reducer';
import {
    selectShowActions,
    selectShowOperdata,
} from 'ConfigurationEditor/reducer';

const mapStateToProps = (state) => {
    const rootModels = selectRootModels(state);
    const childrenNames = selectChildrenNames(state);
    return {
        childrenNames,
        isList: selectIsList(state),
        isLoading: selectIsLoading(state),
        LinkComponent: Link,
        rootModels,
        showActions: selectShowActions(state),
        showOperdata: selectShowOperdata(state),
    };
};

const mapDispatchToProps = dispatch => ({
    onGetChildrenNames(path, matchString = '', showActions = undefined, showOperdata = undefined, chunkSize = undefined) {
        dispatch(getChildrenNamesThunk({
            path,
            matchString,
            showActions,
            showOperdata,
            chunkSize,
        }));
    },
    onReset() {
        dispatch(resetAction());
    },
});

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(Breadcrumb);

export {
    mapStateToProps as _mapStateToProps,
    getChildrenNamesThunk as _getChildrenNamesThunk,
};
