import { connect } from 'react-redux';
import {
    setModelExpandedAction,
} from 'modules/models/actions';
import {
    reloadPackages,
} from 'modules/packages/actions';
import {
    RELOAD_PATH,
} from 'modules/packages/paths';
import {
    selectReadWriteOrRead,
    selectUpgradePending,
} from 'modules/transactions/selectors';
import {
    selectCompletedAt,
    selectError,
    selectResultPending,
} from 'modules/yangActions/selectors';
import {
    selectPackages,
} from 'modules/packages/reducer';
import { selectRootModels, selectRootModelsLoading } from 'modules/models/reducer';
import React from 'react';
import PropTypes from 'prop-types';
import PackagesAndModules from './PackagesAndModules';
import {
    loadStatus,
    selectHasLoaded,
    selectLoadingStatus,
} from './reducer';
import { getModelsAndPackages } from './utils';

const getPackageLinks = (packages) => {
    const packagePrefixes = Object.keys(packages);
    return packagePrefixes.reduce((acc, prefix) => {
        const pkg = packages[prefix];
        const version = pkg.version;
        const status = pkg.status;
        const isOk = status === 'up';
        const errorInfo = pkg.errorInfo;

        return {
            ...acc,
            [prefix]: {
                name: prefix,
                version,
                isOk,
                status,
                errorInfo,
            },
        };
    }, {});
};

class PackagesAndModulesContainer extends React.Component {
    componentDidMount() {
        const { loadingStatus, onLoad, th } = this.props;
        if (th && loadingStatus === loadStatus.NOT_LOADED) {
            onLoad();
        }
    }

    componentDidUpdate(prevProps) {
        const { onLoad, th } = this.props;
        const { th: prevTh } = prevProps;
        if (th && !prevTh) {
            onLoad();
        }
    }

    render() {
        const {
            packages,
            onReloadPackages,
            completedAt,
            reloadError,
            reloadPending,
            rootModels,
            loadingOrUpgradePending,
            loadingRootModels,
        } = this.props;
        const packageLinks = getPackageLinks(packages);
        return (
            <PackagesAndModules
                packageLinks={packageLinks}
                onReload={onReloadPackages}
                completedAt={completedAt}
                reloadError={reloadError}
                reloadPending={reloadPending}
                rootModels={rootModels}
                loadingRootModels={loadingRootModels}
                loadingOrUpgradePending={loadingOrUpgradePending}
            />
        );
    }
}

PackagesAndModulesContainer.propTypes = {
    completedAt: PropTypes.number,
    loadingOrUpgradePending: PropTypes.bool,
    loadingRootModels: PropTypes.bool,
    loadingStatus: PropTypes.number,
    onLoad: PropTypes.func.isRequired,
    onReloadPackages: PropTypes.func.isRequired,
    packages: PropTypes.shape(Object).isRequired,
    reloadError: PropTypes.bool,
    reloadPending: PropTypes.bool,
    rootModels: PropTypes.arrayOf(PropTypes.string),
    th: PropTypes.number,
};

PackagesAndModulesContainer.defaultProps = {
    expandedModels: {},
    models: [],
    packages: {},
};

const mapDispatchToProps = dispatch => ({
    onLoad() {
        return dispatch(getModelsAndPackages());
    },
    onReloadPackages() {
        return dispatch(reloadPackages());
    },
    onSetModelExpanded(modelName, expanded) {
        return dispatch(setModelExpandedAction(modelName, expanded));
    },
});

const mapStateToProps = (state) => {
    const hasLoaded = selectHasLoaded(state);
    const loadingRootModels = selectRootModelsLoading(state);
    const loadingStatus = selectLoadingStatus(state);
    const resultPending = !!selectResultPending(state, RELOAD_PATH);
    const upgradePending = selectUpgradePending(state);
    const reloadPending = !!resultPending || !!upgradePending;
    const loadingOrUpgradePending = !hasLoaded || !!resultPending || !!upgradePending;

    return {
        completedAt: selectCompletedAt(state, RELOAD_PATH),
        loadingOrUpgradePending,
        loadingRootModels,
        loadingStatus,
        packages: selectPackages(state),
        reloadError: !!selectError(state, RELOAD_PATH),
        reloadPending,
        rootModels: selectRootModels(state),
        th: selectReadWriteOrRead(state),
    };
};

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(PackagesAndModulesContainer);

export {
    PackagesAndModulesContainer as _PackagesAndModulesContainer,
    getPackageLinks as _getPackageLinks,
    mapDispatchToProps as _mapDispatchToProps,
    mapStateToProps as _mapStateToProps,
};
