import React from 'react';
import PropTypes from 'prop-types';
import {
    strip,
    truncateLongString,
} from '@webui-one/core/utils/string';
import { pathToObjects } from '@webui-one/core/keypath/kp-parser';
import NavigateHome from '@webui-one/styles/static/NavigateHome';
import NavigateUp from '@webui-one/styles/static/NavigateUp';
import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';
import Link from '../Link';
import BreadcrumbInput from './BreadcrumbInput';
import style from './BreadcrumbStyles';

const getPath = obj => (
    obj.path.indexOf(obj.parentPath) === 0
        ? obj.path.substr(obj.parentPath.length)
        : obj.path
);

const ROOT_PATHS = ['', '/'];
const isRoot = path => ROOT_PATHS.includes(path);

class Breadcrumb extends React.Component {
    constructor(props) {
        super(props);
        this.handleNavigateBreadcrumbs = this.handleNavigateBreadcrumbs.bind(this);
        this.handleGetChildrenNames = this.handleGetChildrenNames.bind(this);
        this.setBreadCrumbPath = this.setBreadCrumbPath.bind(this);
        this.handleNavigate = this.handleNavigate.bind(this);
        this.handleReset = this.handleReset.bind(this);
        this.state = { originalPath: '', breadcrumbPath: '' };
    }

    static getDerivedStateFromProps(props, state) {
        const { path } = props;
        const { originalPath } = state;
        return path === originalPath
            ? null
            : { originalPath: path, breadcrumbPath: path };
    }

    handleBackNavigate() {
        const { onGetChildrenNames } = this.props;
        const { breadcrumbPath } = this.state;
        if (breadcrumbPath !== '') {
            const pathSegments = pathToObjects(breadcrumbPath);
            const newBreadcrumbPath = pathSegments[pathSegments.length - 1].parentPath;
            this.setState({ breadcrumbPath: newBreadcrumbPath });
            onGetChildrenNames(newBreadcrumbPath);
        }
    }

    handleGetChildrenNames(matchString = '', chunkSize = undefined) {
        const { onGetChildrenNames, showActions, showOperdata } = this.props;
        const { breadcrumbPath } = this.state;
        onGetChildrenNames(breadcrumbPath, matchString, showActions, showOperdata, chunkSize);
    }

    handleNavigate(path, isBackNav) {
        if (isBackNav) this.handleBackNavigate();
        else {
            const { onNavigate } = this.props;
            const { breadcrumbPath } = this.state;
            if (path !== '') {
                const newBreadcrumbPath = breadcrumbPath + path;
                const stateBreadBreadcrumbPath = this.setBreadCrumbPath(newBreadcrumbPath);
                onNavigate(pathToObjects(stateBreadBreadcrumbPath).pop().path);
            } else {
                const newPath = isRoot(breadcrumbPath)
                    ? breadcrumbPath
                    : pathToObjects(breadcrumbPath).pop().path;
                onNavigate(newPath);
            }
        }
    }

    handleNavigateBreadcrumbs(path, isBackNav) {
        const { breadcrumbPath } = this.state;
        let newBreadcrumbPath;
        if (isBackNav) {
            if (isRoot(breadcrumbPath)) return;
            const pathSegments = pathToObjects(breadcrumbPath);
            newBreadcrumbPath = pathSegments[pathSegments.length - 1].parentPath;
        } else {
            newBreadcrumbPath = breadcrumbPath + path;
        }
        this.setBreadCrumbPath(newBreadcrumbPath);
    }

    handleReset() {
        const { onReset } = this.props;
        const { originalPath } = this.state;
        this.setState({ breadcrumbPath: originalPath });
        onReset();
    }

    setBreadCrumbPath(newBreadcrumbPath) {
        const { onGetChildrenNames } = this.props;
        const breadcrumbPath = `/${strip(newBreadcrumbPath, '/')}`;
        this.setState({ breadcrumbPath });
        onGetChildrenNames(breadcrumbPath);
        return breadcrumbPath;
    }

    buildBreadcrumbLinks(paths, isList, breadcrumbsCount) {
        const { LinkComponent, toBreadcrumbLink } = this.props;
        let childIsListNode = false;

        const links = paths.reverse().reduce((acc, obj, index) => {
            if (obj.path) {
                const path = obj.path;
                if (!(index === 0 && isList) && !childIsListNode) {
                    acc.push(
                        <span
                            key={`${path}/`}
                            style={index === breadcrumbsCount ? style.blackLink : style.link}
                        >
                            /
                        </span>,
                    );
                }

                childIsListNode = Array.isArray(obj.keys);
                let linkText = strip(getPath(obj), '/');
                if (childIsListNode) {
                    linkText = truncateLongString(linkText, 90, 90);
                }
                const link = toBreadcrumbLink(obj);

                acc.push(
                    <LinkComponent
                        style={index === breadcrumbsCount ? style.blackLink : style.link}
                        key={path}
                        to={link}
                        data-handle={`link ${link}`}
                        onClick={() => this.setBreadCrumbPath(obj.path)}
                    >
                        {linkText}
                    </LinkComponent>,
                );
            }
            return acc;
        }, []);
        return links.reverse();
    }

    render() {
        const {
            childrenNames,
            isList: tempIsList,
            isLoading,
            LinkComponent,
            toBreadcrumbLink,
            rootModels,
        } = this.props;
        const {
            breadcrumbPath,
            originalPath,
        } = this.state;
        const isAtRoot = isRoot(breadcrumbPath);
        const isList = !isAtRoot && tempIsList;
        const breadcrumbsPaths = strip(breadcrumbPath, '/').split(/[/{]/);
        const breadcrumbsCount = (breadcrumbsPaths.length === 1 && breadcrumbsPaths[0] === '')
            ? 0
            : breadcrumbsPaths.length;
        const paths = pathToObjects(`/${strip(breadcrumbPath, '/')}`);
        const links = this.buildBreadcrumbLinks(paths, isList, breadcrumbsCount);
        const upLink = (paths && paths.length > 1) ? paths[1].path : '/';
        const enabled = !isRoot(originalPath);
        return (
            <div style={style.crumbs} data-handle="breadcrumb" data-breadcrumb-path={breadcrumbPath}>
                <LinkComponent
                    to={toBreadcrumbLink({ path: '' })}
                    style={style.navHome}
                    data-handle={`navHome${enabled ? '' : '_disabled'}`}
                >
                    <NavigateHome color={enabled ? defaultStyles.color1 : defaultStyles.color2} />
                </LinkComponent>
                <LinkComponent
                    to={toBreadcrumbLink({ path: upLink })}
                    style={style.navUp}
                    data-handle={`navUp${enabled ? '' : '_disabled'}`}
                >
                    <NavigateUp color={enabled ? defaultStyles.color1 : defaultStyles.color2} />
                </LinkComponent>
                <span style={style.blackLink}>
                    {'/'}
                </span>
                { links }
                {isList && <span style={style.blackLink}>{'{'}</span>}
                <BreadcrumbInput
                    onGetChildrenNames={this.handleGetChildrenNames}
                    childrenNames={isAtRoot ? rootModels : childrenNames}
                    onNavigate={this.handleNavigate}
                    onNavigateBreadcrumbs={this.handleNavigateBreadcrumbs}
                    onReset={this.handleReset}
                    path={breadcrumbPath}
                    isList={isList}
                    isLoading={isLoading}
                />
                {isList && <span style={style.blackLink}>{'}'}</span>}
            </div>
        );
    }
}

Breadcrumb.propTypes = {
    childrenNames: PropTypes.arrayOf(PropTypes.string),
    isList: PropTypes.bool,
    isLoading: PropTypes.bool,
    LinkComponent: PropTypes.elementType,
    path: PropTypes.string,
    onGetChildrenNames: PropTypes.func,
    onReset: PropTypes.func,
    onNavigate: PropTypes.func,
    rootModels: PropTypes.arrayOf(PropTypes.string),
    showActions: PropTypes.bool,
    showOperdata: PropTypes.bool,
    toBreadcrumbLink: PropTypes.func.isRequired,
};

Breadcrumb.defaultProps = {
    LinkComponent: Link,
};

export default Breadcrumb;
