export const selectChildrenNames = state =>
    state.breadcrumbs.childrenNames;
export const selectIsList = state => state.breadcrumbs.isList;
export const selectIsLoading = state => state.breadcrumbs.isLoading;
export const selectSchema = state => state.breadcrumbs.schema;
export const selectHasSchemaFromCurrentPath = (state, path) => state.breadcrumbs.path &&
    state.breadcrumbs.path === path &&
    state.breadcrumbs.schema;
