import React from 'react';
import PropTypes from 'prop-types';
import Loader from '@webui-one/components/Loading/Loader';
import { formatPath } from '@webui-one/core/utils/path';
import { timeStampToISOLocale } from '@webui-one/core/utils/time';
import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';
import Grid from '@webui-one/components/Grid/Grid';
import { Link } from '@webui-one/router';
import useSafeOnClick from '@webui-one/components/hooks/useSafeOnClick';
import Modal from '@webui-one/components/Modal/Modal';
import { packagesAndModules as style } from '@webui-one/styles/ConfigurationEditorStyles';
import {
    RELOAD_ACTION_PATH,
} from 'modules/packages/paths';
import { CONFIGURATION_EDITOR_NAME } from './defines';

const messages = {
    'program-code-error': 'Code error',
    'python-uninitialized': 'Python uninitialized',
    'java-uninitialized': 'Java uninitialized',
    'bad-ncs-version': 'Bad NSO version',
    'missing-dependency': 'Missing dependency',
    'circular-dependency': 'Circular dependency',
    'package-meta-data-error': 'Metadata error',
    'file-load-error': 'File load error',
    'erlang-error': 'Erlang error',
};

const getPackageHeader = (category, modelLinks, openModal) => {
    const modelLink = modelLinks[category];
    const showStatus = modelLink.isOk
        ? !!modelLink.version
        : !!modelLink.status;
    const text = modelLink.isOk
        ? `v${modelLink.version}`
        : messages[modelLink.status] || `Unknown status (${modelLink.status})`;
    const statusStyle = modelLink.isOk
        ? style.packageVersionOk
        : style.packageVersionError;

    return (
        <div key={category} data-handle="module" style={style.moduleChunk}>
            <div data-handle={category} style={style.moduleRow}>
                <div
                    style={style.modulesHeader}
                    data-handle="package-name"
                >
                    {modelLink.name}
                </div>
                { showStatus && !modelLink.isOk && (
                    <button
                        type="button"
                        data-handle="package-version"
                        style={statusStyle}
                        onClick={() => openModal(modelLink.errorInfo)}
                    >
                        { text }
                    </button>
                )}

                {showStatus && modelLink.isOk && (
                    <span
                        data-handle="package-version"
                        style={statusStyle}
                    >
                        { text }
                    </span>
                )}
            </div>
        </div>
    );
};

const renderModels = (rootModels, loadingRootModels) => (
    <div data-handle="modules" style={style.moduleLinks}>
        <div style={style.headerContainer}>
            <div style={style.header}>MODULES</div>
        </div>
        <div style={style.contentContainer}>
            <div style={style.loadingInformation}>
                {loadingRootModels && (
                    <div style={style.loader}>
                        <Loader color={defaultStyles.textColor1} />
                    </div>
                )}
            </div>
            {!loadingRootModels && (
                <Grid asGrid>
                    {rootModels.sort().map(modelName => (
                        <Link
                            to={formatPath(`${CONFIGURATION_EDITOR_NAME}/${modelName}`)}
                            key={modelName}
                            style={style.modelLink}
                        >
                            {modelName}
                        </Link>
                    ))}
                </Grid>
            )}
        </div>
    </div>
);

const renderPackages = (
    packageLinks,
    safeOnClick,
    reloadError,
    reloadPending,
    completedAt,
    loadingOrUpgradePending,
    openModal,
) => (
    <div data-handle="packages" style={style.moduleLinks}>
        <div style={style.headerContainer}>
            <Link
                to={formatPath(`${CONFIGURATION_EDITOR_NAME}/ncs:packages`)}
                style={style.header}
            >
                PACKAGES
            </Link>
            <button
                disabled={loadingOrUpgradePending}
                type="button"
                style={(loadingOrUpgradePending
                    ? style.reloadButtonDisabled
                    : style.reloadButton
                )}
                onClick={safeOnClick}
                data-handle="reload-packages"
            >
                { !loadingOrUpgradePending
                    ? 'Reload'
                    : (
                        <div style={style.loaderButton}>
                            <Loader color={defaultStyles.color3} />
                        </div>
                )}
            </button>
        </div>
        <div style={style.contentContainer}>
            {!reloadError && (reloadPending || completedAt) && (
                <div style={style.loadingInformation}>
                    {reloadPending && (
                        <div style={style.loader}>
                            <Loader color={defaultStyles.textColor1} />
                        </div>
                    )}
                    {completedAt && (
                        <span data-handle="completed-at" >
                            {`Reload completed at ${timeStampToISOLocale(completedAt)}`}
                        </span>
                    )}
                </div>
            )}
            {reloadError && !reloadPending && completedAt && (
                <div data-handle="reload-error" style={style.reloadError}>
                    An error occurred during reload. For detailed information, go to the
                    <Link
                        style={{ color: 'white' }}
                        to={formatPath(RELOAD_ACTION_PATH)}
                    >
                        reload action
                    </Link>
                </div>
            )}
            { Object.keys(packageLinks).length > 0 ? (
                <div>
                    <Grid asGrid>
                        { Object.keys(packageLinks).sort().map(
                            category =>
                            getPackageHeader(
                                category,
                                packageLinks,
                                openModal,
                            ),
                        )}
                    </Grid>
                </div>
            ) : false}
        </div>
    </div>
);

const initialModalState = { isOpen: false, text: '' };

const PackagesAndModules = (
    {
        packageLinks,
        onReload,
        reloadError,
        reloadPending,
        completedAt,
        rootModels,
        loadingRootModels,
        loadingOrUpgradePending,
    },
) => {
    const [modalData, setModalData] = React.useState(initialModalState);
    const closeModal = () => setModalData(initialModalState);
    const openModal = text => setModalData({ isOpen: true, text });
    const safeOnClick = useSafeOnClick(onReload);
    return (
        <div style={style.container}>
            <Modal
                title="Error info text"
                shouldRender={modalData.isOpen}
                onRequestClose={closeModal}
            >
                <div data-handle="error-info">
                    {modalData.text}
                </div>
            </Modal>

            { renderPackages(
              packageLinks,
              safeOnClick,
              reloadError,
              reloadPending,
              completedAt,
              loadingOrUpgradePending,
              openModal) }

            { renderModels(rootModels, loadingRootModels) }
        </div>
    );
};

PackagesAndModules.propTypes = {
    loadingOrUpgradePending: PropTypes.bool,
    packageLinks: PropTypes.shape({}).isRequired,
    onReload: PropTypes.func.isRequired,
    reloadError: PropTypes.bool,
    reloadPending: PropTypes.bool,
    completedAt: PropTypes.number,
    rootModels: PropTypes.arrayOf(PropTypes.string),
    loadingRootModels: PropTypes.bool,
};

export default PackagesAndModules;
